import React, { useCallback, useState } from "react";
import Register from "../../../components/Auth/Register";
import useStore from "../../../lib/hooks/useStore";
import Swal from "sweetalert2";

interface RegisterContainerProps {
  changePage: () => void;
}

const RegisterContainer = ({ changePage }: RegisterContainerProps) => {
  const { store } = useStore();
  const { tryRegister, tryAccredit } = store.AuthStore;

  const [email, setEmail] = useState<string>("");
  const [accredit, setAccredit] = useState<boolean>(false);
  const [loading, setLoading] = useState<boolean>(false);

  const [name, setName] = useState<string>("");
  const [pw, setPw] = useState<string>("");
  const [checkPw, setCheckPw] = useState<string>("");

  const handleRegister = useCallback(async () => {
    if (!email || !pw || !name) {
      Swal.fire({ icon: "error", title: " ", text: "   !" });
    } else if (pw !== checkPw) {
      Swal.fire({ icon: "error", title: " ", text: "  !" });
    } else if (accredit === false) {
      Swal.fire({ icon: "error", title: " ", text: "  !" });
    } else {
      tryRegister(email, name, pw)
        .then((res) => {
          Swal.fire({ icon: "success", title: " ", text: " !" });
          changePage();
        })
        .catch((err) => {
          Swal.fire({ icon: "error", title: " ", text: "    !" });
        });
    }
  }, [email, pw, name, accredit, checkPw]);

  const handleEmailAccredit = () => {
    setLoading(true);
    if (!email) {
      Swal.fire({ icon: "error", title: "", text: "  " });
      setLoading(false);
    } else {
      tryAccredit(email)
        .then((res) => {
          setLoading(false);
          Swal.fire(" !", "   !", "success");
        })
        .catch((err) => {
          Swal.fire({ icon: "error", title: "  ", text: "  !" });
        });
    }
  };

  return (
    <>
      <Register
        changePage={changePage}
        email={email}
        setEmail={setEmail}
        name={name}
        setName={setName}
        pw={pw}
        setPw={setPw}
        checkPw={checkPw}
        setCheckPw={setCheckPw}
        handleRegister={handleRegister}
        handleEmailAccredit={handleEmailAccredit}
        setAccredit={setAccredit}
        loading={loading}
      />
    </>
  );
};

export default RegisterContainer;
