import React from "react";
import "./WriteComment.scss";
import { FiPlus } from "react-icons/fi";
import { writeColor } from "../../../models/colorTemplate";
import Button from "../../common/Button/Button";
import SignatureCanvas from "react-signature-canvas";
import { Link } from "react-router-dom";
import PaperType from "../../../util/types/Paper";
import ChangePositionContainer from "../../../containers/Paper/ChangePosition/ChangePositionContainer";

interface WriteCommentProps {
  handleImageChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
  preview: string | ArrayBuffer | null;
  color: string;
  setColor: React.Dispatch<React.SetStateAction<string>>;
  write: string;
  setWrite: React.Dispatch<React.SetStateAction<string>>;
  font: string;
  setFont: React.Dispatch<React.SetStateAction<string>>;
  paperInfo?: PaperType;
  isPosition: boolean;
  setComment: React.Dispatch<React.SetStateAction<string>>;
  comment: string;
  canvasEl: React.RefObject<SignatureCanvas>;
  nextPosition: () => void;
}

const WriteComment = ({
  handleImageChange,
  preview,
  comment,
  setComment,
  color,
  setColor,
  write,
  setWrite,
  font,
  setFont,
  paperInfo,
  isPosition,
  canvasEl,
  nextPosition
}: WriteCommentProps) => {
  return (
    <>
      {isPosition ? (
        <>
          <ChangePositionContainer />
        </>
      ) : (
        <>
          {paperInfo && paperInfo.member ? (
            <div className="write">
              <div className="write-box">
                <div className="write-box-title">{paperInfo.member.name}   .</div>
                <div className="write-box-way">
                  <div className="write-box-way-text"></div>
                  <div className="write-box-way-area">
                    <label className="write-box-way-area-select">
                      
                      <input
                        type="radio"
                        name="way"
                        value="text"
                        checked={write === "text"}
                        onChange={(e) => setWrite(e.target.value)}
                      />
                      <span className="write-box-way-area-select-radio" />
                    </label>
                    <label className="write-box-way-area-select">
                      
                      <input
                        type="radio"
                        name="way"
                        value="image"
                        checked={write === "image"}
                        onChange={(e) => setWrite(e.target.value)}
                      />
                      <span className="write-box-way-area-select-radio" />
                    </label>
                    <label className="write-box-way-area-select">
                      
                      <input
                        type="radio"
                        name="way"
                        value="hand"
                        checked={write === "hand"}
                        onChange={(e) => setWrite(e.target.value)}
                      />
                      <span className="write-box-way-area-select-radio" />
                    </label>
                  </div>
                </div>
                {write === "text" ? (
                  <div className="write-box-way">
                    <div className="write-box-way-text"></div>
                    <div className="write-box-way-area">
                      <label className="write-box-way-area-select">
                        NotoSans
                        <input
                          type="radio"
                          name="font"
                          value="NotoSansKR"
                          checked={font === "NotoSansKR"}
                          onChange={(e) => setFont(e.target.value)}
                        />
                        <span className="write-box-way-area-select-radio" />
                      </label>
                      <label className="write-box-way-area-select">
                        
                        <input
                          type="radio"
                          name="font"
                          value=""
                          checked={font === ""}
                          onChange={(e) => setFont(e.target.value)}
                        />
                        <span className="write-box-way-area-select-radio" />
                      </label>
                      <label className="write-box-way-area-select">
                        
                        <input
                          type="radio"
                          name="font"
                          value="_ac"
                          checked={font === "_ac"}
                          onChange={(e) => setFont(e.target.value)}
                        />
                        <span className="write-box-way-area-select-radio" />
                      </label>
                    </div>
                  </div>
                ) : (
                  ""
                )}

                {write === "image" ? (
                  ""
                ) : (
                  <div className="write-box-color">
                    <div className="write-box-color-name"></div>
                    <div className="write-box-color-area">
                      {writeColor.map((color: string, index: number) => (
                        <div key={index} style={{ backgroundColor: color, cursor: "pointer" }} onClick={() => setColor(color)} />
                      ))}
                      <label htmlFor="write-color-picker">
                        <input
                          id="write-color-picker"
                          className="write-box-color-area-picker"
                          type="color"
                          onChange={(e) => setColor(e.target.value)}
                        />
                        <FiPlus />
                      </label>
                    </div>
                  </div>
                )}

                <div className="write-box-writing">
                  {write === "text" ? (
                    <textarea
                      autoFocus
                      className="write-box-textbox"
                      placeholder=" ."
                      style={{ color: color, fontFamily: font }}
                      value={comment}
                      onChange={(e) => setComment(e.target.value)}
                    />
                  ) : write === "image" ? (
                    <div className="write-box-writing-profile">
                      <label htmlFor="file"> </label>
                      <input onChange={handleImageChange} id="file" type="file" accept="image/png, image/jpeg" />
                      {preview ? (
                        <img src={preview.toString()} className="write-box-writing-profile-preview" />
                      ) : (
                        <div className="write-box-writing-profile-preview"></div>
                      )}
                    </div>
                  ) : (
                    <SignatureCanvas ref={canvasEl} penColor={color} canvasProps={{ className: "write-box-writing-sigCanvas" }} />
                  )}
                </div>

                <div className="write-box-btnarea">
                  <Button text="" style={{ height: "3.6rem", fontSize: "1.2rem" }} onClick={() => nextPosition()} />
                </div>
              </div>
            </div>
          ) : (
            <div>loading..</div>
          )}
        </>
      )}
    </>
  );
};

export default WriteComment;
